/* variables */
/* inject:mixins:less */
/* endinject */
/* base */
/*---------- TYPHOGRAFY ----------*/
/* Root elements (use pixels) */
html {
  font-size: 17px !default;
}
/* Standard document elements (use rem) */
.header {
  font-size: 1.5rem;
}
.footer {
  font-size: 0.75rem;
}
.sidebar {
  font-size: 0.85rem;
}
/* Standard module elements (use em) */
h1 {
  font-size: 3em !default;
}
h2 {
  font-size: 2.5em !default;
}
h3 {
  font-size: 2em !default;
}
h4 {
  font-size: 1.5em !default;
}
h5 {
  font-size: 1em !default;
}
#description {
  background-color: #36a;
}
#program {
  background-color: #a36;
}
#main_sponsor {
  background-color: #6a3;
}
#prices {
  background-color: #ccc;
}
#main_partner {
  background-color: #971;
}
#partners {
  background-color: #197;
}
#sponsors {
  background-color: #719;
}
.container,
.container-fluid {
  position: relative;
  font-size: 1rem;
}
.container {
  width: 90%;
  max-width: 1200px;
}
.container-fluid {
  width: 100%;
}
@media (max-width: 599px) {
  .container {
    font-size: 0.9rem;
    width: 100%;
  }
}
@media (max-width: 400px) {
  .container {
    font-size: 0.8rem;
  }
}
/* inject:components:less */
nav#et-navbar {
  position: relative;
  font-size: 14px;
  height: 5em;
  background-color: #fefefe;
}
nav#et-navbar > .container {
  position: relative;
  max-width: 1024px;
  margin: 0 auto;
  height: inherit;
  display: flex;
}
nav#et-navbar .brand {
  width: 4.5em;
  display: flex;
  align-items: center;
  flex: 0 0 auto;
}
nav#et-navbar .brand > img {
  width: 100%;
  box-sizing: border-box;
  padding: 0.2em 1em;
}
nav#et-navbar .navbar-nav {
  position: relative;
  cursor: pointer;
  padding: 0 1em;
  user-select: none;
  color: #a03033;
  display: flex;
  align-items: center;
  overflow: hidden;
  background-color: transparent;
  transition: background-color 0.4s ease-in-out;
}
nav#et-navbar .navbar-nav > * {
  padding: 0.1em;
}
nav#et-navbar .navbar-nav:hover {
  background-color: rgba(160, 48, 51, 0.08);
}
@media (max-width: 650px) {
  nav#et-navbar > .container {
    max-width: 500px;
  }
  nav#et-navbar > .container > .brand {
    width: 4em;
  }
  nav#et-navbar > .container > .navbar-nav {
    flex-grow: 1;
  }
  nav#et-navbar > .container > .navbar-nav > i {
    margin: 0 auto;
  }
  nav#et-navbar > .container > .flex,
  nav#et-navbar > .container .navbar-nav > span {
    display: none;
  }
}
/* endinject */
/* inject:partials:less */
.board-links {
  width: 100%;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}
.board-cards-container {
  display: flex;
  justify-content: center;
}
.board-cards-container .board-cards {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  max-width: 1500px;
}
.board-cards-container .board-cards .board-card {
  display: flex;
  align-items: center;
  background-image: url("../../../assets/images/ETlogos/logo.svg");
  background-position: left;
  background-repeat: no-repeat;
  background-size: 7em;
  width: 300px;
  margin: 0px;
  padding: 0px;
}
.board-cards-container .board-cards .board-card .board-card-image {
  height: 80px;
  width: 80px;
  border-radius: 50%;
  background-size: cover;
  margin: 10px 20px;
  border: 1px solid #a03033;
}
.board-cards-container .board-cards .board-card a {
  text-decoration: none;
  color: #a03033;
}
.board-cards-container .board-cards .board-card name {
  font-weight: bold;
}
#board-background {
  background-color: #ef5350;
}
@media (max-width: 400px) {
  #page-companies .md-display-1 {
    font-size: 30px;
    margin: 30px 0 5px 0;
  }
}
@media (max-width: 350px) {
  #page-companies .md-display-1 {
    font-size: 25px;
  }
}
#page-companies .company-widget {
  padding: 20px 0;
  border-top: 1px solid rgba(150, 150, 150, 0.4);
}
#page-companies .company-widget:first-of-type {
  border-top: none;
}
.login-container {
  margin: 0 auto;
  padding: 50px 0;
  max-width: 300px;
  text-align: center;
}
.login-container .login-btn-group-vertical > .btn {
  margin: 2px 0;
}
.login-container .login-header {
  margin: 25px 0 5px 0;
}
.login-container .login-content {
  padding: 15px 5px;
}
.login-container .login-separator {
  margin: 15px 0;
}
.login-container .login-separator:before,
.login-container .login-separator:after {
  background-color: #aaa;
  content: "";
  display: inline-block;
  height: 1px;
  position: relative;
  vertical-align: middle;
  width: 40%;
}
.login-container .login-separator > .separator-text {
  padding: 0 15px;
}
.video-wrapper {
  position: relative;
  padding-bottom: 56.25%;
  /* 16:9 */
  height: 0;
}
.video-wrapper > iframe {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.programme-header {
  background-color: #fff;
  font-size: 1rem;
}
.programme-header > .header-content {
  margin-top: 20px;
}
.programme-header > .header-content img {
  flex: 0 0 auto;
  margin: 0 2em;
  width: 5em;
}
.programme-header > .header-content .md-display-2 {
  font-size: 3em;
  line-height: 2em;
}
.programme-header > .header-content .md-title {
  font-size: 2.2em;
  margin-left: 2.5em;
}
.skew-lb,
.skew-rb {
  position: relative;
}
.skew-lb > *,
.skew-rb > * {
  z-index: 1;
}
.skew-lb:before,
.skew-rb:before {
  content: '';
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: inherit;
  top: 50%;
}
.skew-lb:before {
  transform: skewY(-3deg);
}
.skew-rb:before {
  transform: skewY(3deg);
}
@media (min-width: 600px) {
  #promo-section {
    background: url('/assets/images/ETlogos/logo.svg') no-repeat;
    background-size: 50% 250%;
    background-position: 100% 50%;
    padding: 3% 0;
  }
}
/* endinject */
